.popup--back{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 50;
}
.popup--content{
    width: 60%;
    height: 400px;
    background-color:white ;
    border-radius: 8px;
    position: relative;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
}
.popup--close{
    font-weight: bold;
    font-size: 30px;
    position: absolute;
    right: 20px;
    cursor: pointer;
}